<template>
	<div id="login">
		<div class="uk-container">
			<div class="uk-position-center" style="width: 50%;">
				<div class="uk-card uk-card-primary uk-card-body uk-text-center">
					<h2>QUEUEING MANAGEMENT SYSTEM</h2>
					<form @submit.prevent="login">
				    <fieldset class="uk-fieldset">
			        <legend class="uk-legend">ADMIN / COUNTER</legend>
			        <div class="uk-margin">
		            <input class="uk-input" type="text" placeholder="Login ID:" v-model="email">
			        </div>
			        <div class="uk-margin">
		            <input class="uk-input" type="password" placeholder="Password:" v-model="password">
			        </div>
			        <div class="uk-margin">
		            <button class="uk-button uk-button-default" type="submit">LOGIN</button>
			        </div>
				    </fieldset>
					</form>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
export default {
	data () {
		return {
			email: '',
			password: ''
		}
	},
	mounted () {
		this.checkAuth()
	},
	methods: {
		async login () {
			try {
				let result = await app.authenticate({
					strategy: 'local',
					email: this.email,
					password: this.password
				})
				if (result.user.type == 'employee') {
					window.location.href = '/counter'
				} else {
					window.location.href = '/admin'
				}
				console.log()
			} catch (err) {
				alert (err.message)
			}
		},
		async checkAuth () {
			try {
				let result = await isAuth()
				console.log(result.user)
			} catch (error) {
				console.log(error)
			}
		}
	}
}
</script>
<style>
#login {
	background-image: url('https://i.pinimg.com/originals/cd/c8/de/cdc8de777d6705a71853293304c5436f.jpg');
	height: 100vh;
}
.uk-card {
	border-radius: 60px 0 60px 0;
	background: #1a7b9e;
}
</style>


