<template>
	<div id="counter">
		<div class="uk-container">
			<div class="uk-child-width-1-3@m uk-grid-small uk-grid-match uk-margin-large-top uk-flex-center" uk-grid>
			    <!-- <div>
			        <div class="uk-card uk-card-default uk-card-body">
			            <h3 class="uk-card-title">OH HOLD CARD</h3>
			            <p style="font-size: 90px;">123</p>
			        </div>
			    </div> -->
			    <div>
			        <div class="uk-card uk-card-primary uk-card-body uk-text-center">
			            <h3 class="uk-card-title" style="font-size: 150px; font-weight: bold;">{{currentTicket}}</h3>
			            <p style="font-size: 40px;">COUNTER 1</p>
			        </div>
			    </div>
			    <!-- <div>
			        <div class="uk-card uk-card-default uk-card-body">
			            <h3 class="uk-card-title">OH HOLD CARD</h3>
			            <p style="font-size: 90px;">121</p>
			        </div>
			    </div> -->
			</div>
			<div class="uk-margin-small-top uk-text-center">
				<button class="uk-button uk-button-primary uk-button-large">PREV CARD</button>
				<button class="uk-button uk-button-primary uk-button-large" @click="getNextTicket">NEXT CARD</button>
			</div>
		</div>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
const getNextTicketService = app.service('get-next-ticket')
export default {
	data () {
		return {
			currentTicket: '000',
			user: '',
			counterID: '',
			ticketRetrieved: true
		}
	},
	async mounted () {
		try {
			let result = await isAuth()
			if (!result.accessToken) { window.location.href = '/login' }
			this.user = result.user
			this.retainTicket()
		} catch (error) {
			console.log(error)
		}
	},
	methods: {
		async getNextTicket () {
			try {
				let result = await app.service('get-next-ticket').find()
				console.log(result._id)
				localStorage.setItem('ticket', result.label)
				this.currentTicket = result.label
			} catch (error) {
				console.log(error)
			}
		},
		async retainTicket () {
			if (localStorage.getItem('ticket')) {
				this.currentTicket = localStorage.getItem('ticket')
			}
		}
	}
}
</script>
<style scoped>
#counter {
	background-image: url('https://i.pinimg.com/originals/cd/c8/de/cdc8de777d6705a71853293304c5436f.jpg');
	height: 100vh;
}
</style>


